// Color
$color: (
	'base'   : #f00,
	'blue_1' : #4083cd,
	'blue_2' : #5c9dbc,
	'gray_1' : #fafafa,
);
$color_template: (
	'orange_1' : #f06262,
	'red_1'    : #f2506e,
	'green_1'  : #009a9a,
);
$color_custom: (
	'1' : #dba800,
	'2' : #d24488,
	'3' : #7c608c,
	'4' : #2985be,
	'5' : #a3bc5c,
	'6' : #d74f13,
);
$color_base   : map-get($color, 'base');
$color_main_1 : map-get($color, 'blue_1');
$color_main_2 : map-get($color, 'blue_2');

// Font
$font_base_sans  : 'Arial',
                    YuGothic, 'Yu Gothic',
                   'Hiragino Kaku Gothic ProN', 'ヒラギノ角ゴ ProN W3',
                   'メイリオ', Meiryo,
                   'ＭＳ ゴシック';
$font_ie_sans    : 'Arial',
                   'Hiragino Kaku Gothic ProN', 'ヒラギノ角ゴ ProN W3',
                   'メイリオ', Meiryo,
                   'ＭＳ ゴシック';
$font_web_1      : 'Noto Serif JP';
$font_base       : $font_base_sans;
$font_base_serif : $font_web_1,
                   YuMincho,
                   'Hiragino Mincho ProN W3', 'Hiragino Mincho ProN',
                   'HG明朝E',
                   'ＭＳ Ｐ明朝', 'ＭＳ 明朝';
$font_serif_1    : $font_web_1,
                   $font_base_serif,
                   serif;

// Height
$site_header_height    : 72px;
$site_header_height_sp : 56px;
$wp_loginbar_height    : 32px;
$wp_loginbar_height_sp : 45px;

// Width
$container_width: (
	'main'      : 1080px,
	'larger_1'  : 1280px,
	'smaller_1' : 960px,
	'smaller_2' : 640px,
);
$container_width_main: map-get($container_width, 'main');

// size
$wp_gutenburg_base_space_size : 2em;
$mq_gutter_val                : 16px;

// String
$detect_primary_size : 'MaxS';

// Other
$break_points: (
	'Min1120' : 1120px,
	'MinL'    : 992px,
	'MinM'    : 768px,
	'MinS'    : 576px,
	'Max1120' : 1119.98px,
	'MaxL'    : 991.98px, // タブレットサイズ >
	'MaxM'    : 767.98px, // タブレットサイズ > スマホ横 >
	'MaxS'    : 575.98px, // スマホサイズ
	'Max1239Min770': (
		'Max' : 1238.98px,
		'Min' : 770px
	),
);
