/**
 * reset
 */


*,
*::before,
*::after
{
	letter-spacing: .05em;
	vertical-align: baseline;
	padding: 0;
	border: 0;
	margin: 0;
	box-sizing: border-box;

	// outline: 1px solid red;
}

*::before,
*::after
{
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
	letter-spacing: inherit;
}

*::selection {
	color: #fff;
	background-color: $color_main_1;
}

#wpadminbar *::before,
#wpadminbar *::after
{
	line-height: 1;
}

html {
	font-family: sans-serif;
	font-size: 16px;
	-webkit-text-size-adjust: 100%;
	-webkit-tap-highlight-color: rgba($color_main_1, .3);

	// &[data-site-set-fontsize="S"] {
	// 	font-size: 58%;
	// }

	// &[data-site-set-fontsize="L"] {
	// 	font-size: 72%;
	// }
}

article,
aside,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
time,
picture
{
	display: block;
}

body {
	font-size: 16px;
	font-family: $font_base, sans-serif;
	font-weight: 400;
	color: $color_base;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: left;
	// min-width: calc( #{map-get($container_width, 'main')} + 40px );
	min-width: 375px;

	&.-liquid {
		min-width: 100%;
	}

	&.-opened_hamburgerMenu {
		overflow: hidden;
	}

	@include ie_hack {
		font-family: $font_ie_sans, sans-serif;
	}
}

[tabindex="-1"]:focus:not(:focus-visible) {
	outline: 0 !important;
}

hr {
	box-sizing: content-box;
	height: 0;
	overflow: visible;
}

h1,
h2,
h3,
h4,
h5,
h6,
li
{
	letter-spacing: .075em;
}

h1,
h2,
h3,
h4,
h5,
h6
{
	font-feature-settings: 'palt';
}

p {
	line-height: 2;
	letter-spacing: .075em;
}

abbr[title],
abbr[data-original-title]
{
	text-decoration: underline;
	text-decoration: underline dotted;
	cursor: help;
	border-bottom: 0;
	-webkit-text-decoration-skip-ink: none;
	text-decoration-skip-ink: none;
}

address {
	font-style: normal;
	line-height: inherit;
}

ul,
ol
{
	padding-left: 2em;

	&[class*='_list_'] {
		margin-top: 1em;
		margin-bottom: 1em;

		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	@include mq {
		padding-left: 1.5em;
	}
}

ol ol,
ul ul,
ol ul,
ul ol
{
	margin-top: .5em;
	margin-bottom: .5em;
}

b,
strong,
a
{
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
	letter-spacing: inherit;
}

b,
strong
{
	font-weight: bolder;
}

small {
	font-size: .8em;
}

sub,
sup
{
	position: relative;
	font-size: .75em;
	line-height: 0;
	vertical-align: baseline;
}

sub {
	bottom: -.25em;
}

sup {
	top: -.5em;
}

a,
button,
input,
textarea
{
	&:focus {
		outline-color: $color_main_1;
	}
}

a {
	color: inherit;
	text-decoration: none;
}

pre,
code,
kbd,
samp
{
	font-family: SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;
	font-size: 1em;
}

pre {
	overflow: auto;
}

source {
	width: 100%;
	display: block;
}

img,
iframe
{
	max-width: 100%;
	vertical-align: bottom;
}

img {
	height: auto;
}

svg {
	overflow: hidden;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
	width: 100%;
}

caption {
	padding-top: .75rem;
	padding-bottom: .75rem;
	color: #6c757d;
	text-align: left;
	caption-side: bottom;
}

th {
	text-align: inherit;
}

label {
	display: inline-block;
	cursor: pointer;

	body[class*='confirm'] form & {
		pointer-events: none;
	}
}

button {
	background-color: transparent;
	border-radius: 0;
}

input,
button,
select,
optgroup,
textarea
{
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
	letter-spacing: inherit;
}

button,
input
{
	overflow: visible;
}

button,
select
{
	text-transform: none;
}

select {
	word-wrap: normal;
}

button,
[type="button"],
[type="reset"],
[type="submit"]
{
	appearance: button;
}

button,
[type="button"],
[type="reset"],
[type="submit"]
{
	&:not(:disabled) {
		cursor: pointer;
	}
}

button,
[type="button"],
[type="reset"],
[type="submit"]
{
	&::-moz-focus-inner {
		padding: 0;
		border-style: none;
	}
}

input[type="radio"],
input[type="checkbox"]
{
	box-sizing: border-box;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"]
{
	appearance: listbox;
}

textarea {
	overflow: auto;
	resize: vertical;
}

fieldset {
	min-width: 0;
}

legend {
	font-size: 15px;
	white-space: normal;
	line-height: inherit;
	color: inherit;
	display: block;
	width: 100%;
	max-width: 100%;
}

progress {
	vertical-align: baseline;
}

[type="number"],
[type="number"]
{
	&::-webkit-inner-spin-button {
		height: auto;
	}
}

[type="search"] {
	outline-offset: -2px;
	appearance: none;

	&::-webkit-search-decoration {
		appearance: none;
	}
}

::-webkit-file-upload-button {
	font: inherit;
	appearance: button;
}

output {
	display: inline-block;
}

summary {
	display: list-item;
	cursor: pointer;
}

template {
	display: none;
}
